<template>
    <v-sheet
      rounded="lg"
      class="w-primary"
      min-height="85vh"
    >
      <v-container>
        <v-row>
          <v-col>
            <v-card
              class="w-secondary"
              min-height="85vh"
            >
            <v-card-title class="w-text  w-primary">
                <span><v-icon>mdi-head-lightbulb-outline</v-icon> Innovations</span>
            </v-card-title>
              <v-virtual-scroll
                :items="generalNews"
                max-height="85vh"
                item-height="50"
              >
                <template v-slot:default="{ item }">
                  <v-list-item @click="openBlogDialog(item.EXTENDEDDATA)" :key="item.DATAID">    
                    <v-list-item-content>
                      <v-list-item-title>
                       <strong>{{ item.NAME }}</strong>
                      </v-list-item-title>
                    </v-list-item-content>
                  </v-list-item>
                  <v-divider></v-divider> 
                </template>
              </v-virtual-scroll>
            </v-card>
          </v-col>
        </v-row>
      </v-container> 
      <v-dialog
         v-model="innovationDialog"
         fullscreen
         hide-overlay
         transition="dialog-bottom-transition"
       >
         <template v-slot:activator="{ on, attrs }">
             <v-btn style="visibility: hidden;" v-bind="attrs" v-on="on"></v-btn>
         </template>
          <v-card>
             <v-toolbar
               dark
               color="primary"
             >
               <v-btn
                 icon
                 dark
                 @click="innovationDialog = false"
               >
                 <v-icon>mdi-close</v-icon>
               </v-btn>
               <v-toolbar-title>Innovation</v-toolbar-title>
             </v-toolbar>  
             <v-card-text><div v-html="innovationContent"></div></v-card-text>                    
           </v-card>
       </v-dialog> 
    </v-sheet>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
export default {
  name: 'HRNews',
  data: () => ({ 
    innovationDialog: false,
    innovationContent: ''
  }),
  computed: {
    ...mapGetters('admin', [
            'generalNews'
    ])
  },
  methods: {
    openBlogDialog: function(data){
      this.innovationContent = '';
      this.innovationDialog = true;
      this.innovationContent = JSON.parse(data).Story
    }
  },
  created: function() {
  }
}
</script>

<style lang="scss" scoped>
  .w-primary{
    background-color: #eee;
  }
  .w-secondary{
    background-color: #f7f8f8;
  }
   .w-text{
    color: black;
  }
</style>
