<template>
    <v-sheet
      rounded="lg"
      class="w-primary"
      min-height="85vh"
    >
      <v-container>
        <v-row>
          <v-col>
            <v-card
              class="w-secondary"
              min-height="85vh"
            >
            <v-card-title class="w-text w-primary">
                <span><v-icon>mdi-account-group</v-icon> HR Vacancies</span>
            </v-card-title>
              <v-virtual-scroll
                :items="vacancyHeadlines"
                max-height="85vh"
                item-height="50"
              >
                <template v-slot:default="{ item }">
                  <v-list-item @click="openBlogDialog(item.Id)" :key="item.No">     
                    <v-list-item-content>
                      <v-list-item-title>
                       <strong>{{ item.Headline }}</strong>
                      </v-list-item-title>
                    </v-list-item-content>
                  </v-list-item>
                  <v-divider></v-divider>
                </template>
              </v-virtual-scroll>
            </v-card>
          </v-col>
        </v-row>
      </v-container>  
       <v-dialog
         v-model="dialog"
         fullscreen
         hide-overlay
         transition="dialog-bottom-transition"
       >
         <template v-slot:activator="{ on, attrs }">
             <v-btn style="visibility: hidden;" v-bind="attrs" v-on="on"></v-btn>
         </template>
          <v-card>
             <v-toolbar
               dark
               color="primary"
             >
               <v-btn
                 icon
                 dark
                 @click="dialog = false"
               >
                 <v-icon>mdi-close</v-icon>
               </v-btn>
               <v-toolbar-title>HR News</v-toolbar-title>
             </v-toolbar>  
             <v-card-text><div v-html="content"></div></v-card-text>                    
           </v-card>
       </v-dialog> 
    </v-sheet>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import Vue from 'vue'
export default {
  name: 'HRNews',
  data: () => ({ 
    dialog: false,
    content: ''
  }),
  computed: {
    ...mapGetters('hr', [
            'vacancyHeadlines'
    ])
  },
  methods: {
    openBlogDialog: async function(id){
       console.log('Opened');
       var res =  await Vue.prototype.$api.general.get(opentext.webreportIds.blogContentId, "&dataId="+id);
       this.content = JSON.parse(res[0].EXTENDEDDATA).blogContent;
       this.dialog = true;
    }
  },
  created: function() {
  }
}
</script>

<style lang="scss" scoped>
  .w-primary{
    background-color: #eee;
  }
  .w-secondary{
    background-color: #f7f8f8;
  }
   .w-text{
    color: black;
  }
</style>
